<template>
  <div>
      <h1>form组件的使用</h1>
        <el-radio-group v-model='val' @change="aa" text-color="red">
            <el-radio-button label="男" border></el-radio-button>
            <el-radio-button label="女" border disabled></el-radio-button>
            <el-radio-button label="人妖" border></el-radio-button>
        </el-radio-group>
        <h1>复选框的使用</h1>
        <el-checkbox label="复选" disabled></el-checkbox>
        <hr>
        <el-checkbox-group v-model="arrs">
            <el-checkbox label="复选框a"></el-checkbox>
            <el-checkbox label="复选框b"></el-checkbox>
            <el-checkbox label="复选框c"></el-checkbox>
            <el-checkbox label="禁用" disabled></el-checkbox>
            <el-checkbox label="选中且禁用" disabled></el-checkbox>

        </el-checkbox-group>
  </div>
</template>

<script>
export default {
    name:'FormExam',
    data(){
        return {
            val:'男',
            arrs:[
                '复选框a','选中且禁用'
            ]
        }
    },
    methods:{
        aa:function(){
            return {
                val:'男'
            }
        }
    }
}
</script>

